import React, { FC, useRef, useState } from 'react'
import { CSSTransition, TransitionGroup } from 'react-transition-group'
import { YoukuPlaceholder } from './Placeholder'
import { CardCommonProps, Container } from '../base'
import styled from 'styled-components'
import { BrowserIcon, RefreshIcon } from '../../icon'

export { BG_URL } from './Placeholder'

export interface YoukuCardProps extends CardCommonProps {
  // storybook 中有示例
  locale: {
    open: string
    confirm: string
    name: string
    linkText: string
    actionText: string
    placeholder: string
    copyTitle: string
    copyDesc: string
    linkError: string
    linkErrorDesc: string
  }
}

const ContentWrapper = styled.div`
  position: relative;
  height: 100%;
  border-radius: 8px;
  overflow: hidden;
`

export const Content = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  &.tran-enter {
    opacity: 0;
  }

  &.tran-enter-active {
    opacity: 1;
    transition: opacity 200ms ease-in-out;
  }

  &.tran-exit {
    opacity: 1;
  }

  &.tran-exit-active {
    opacity: 0;
    transition: opacity 200ms ease-in-out;
  }
`

const ToolbarItemWrapper = styled.div`
  padding: 2px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  cursor: pointer;

  > div {
    width: 24px;
    height: 24px;
    border-radius: 4px;
    &:hover {
      background-color: rgba(25, 31, 37, 0.08);
    }
    > svg {
      width: 100%;
      height: 100%;
    }
  }
`
const ToolbarItem: FC<{
  onClick?: () => void
}> = ({ children, onClick }) => {
  return (
    <ToolbarItemWrapper onClick={onClick}>
      <div>{children}</div>
    </ToolbarItemWrapper>
  )
}

// 底部留出一段距离，这样鼠标划出以后，toolbar 不会消失
const ToolbarWrapper = styled.div`
  padding-bottom: 10px;
  position: absolute;
  left: 0;
  top: -35px;
  z-index: 9999;
`
const ToolbarContent = styled.div`
  border: 1px solid rgba(17, 31, 44, 0.08);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  display: flex;
  align-items: center;
  background: white;
`
export const Toolbar = ({
  onRefresh,
  onOpenURL,
}: {
  onRefresh: () => void
  onOpenURL: () => void
}) => {
  return (
    <ToolbarWrapper>
      <ToolbarContent>
        <ToolbarItem onClick={onRefresh}>
          <RefreshIcon />
        </ToolbarItem>

        <ToolbarItem onClick={onOpenURL}>
          <BrowserIcon />
        </ToolbarItem>
      </ToolbarContent>
    </ToolbarWrapper>
  )
}

export const getHeight = ({ isMobile }: { isMobile: boolean }) => {
  return isMobile ? 200 : 400
}

interface YoukuContentHandle {
  refresh: () => void
}

const YoukuContentWrapper = styled.div`
  height: 100%;
  display: flex;
  align-items: center;
  background: white;
`
export const YoukuContent = React.forwardRef<
  YoukuContentHandle,
  {
    url: string
  }
>(({ url }, ref) => {
  const iframeRef = useRef<HTMLIFrameElement>(null)

  React.useImperativeHandle(
    ref,
    (): YoukuContentHandle => ({
      refresh: () => {
        if (iframeRef.current) {
          iframeRef.current.src += ''
        }
      },
    })
  )

  return (
    <YoukuContentWrapper>
      <iframe
        onLoad={(e) => (e.target as HTMLIFrameElement).blur()}
        sandbox="allow-forms allow-scripts allow-same-origin"
        allowFullScreen={true}
        ref={iframeRef}
        style={{
          border: 'none',
          width: '100%',
          height: '100%',
        }}
        src={url}
      />
    </YoukuContentWrapper>
  )
})

export const YoukuCard: FC<YoukuCardProps> = ({
  isMobile = false,
  readonly = false,
  url,
  locale,
  onURLChange,
  onGoToSite,
  openURLInBrowser,
}) => {
  const height = getHeight({ isMobile })
  const [showToolbar, setShowToolbar] = useState(false)
  const showPlaceholder = url === ''
  const contentRef = useRef<YoukuContentHandle>(null)

  const toolbarVisible = (() => {
    if (readonly) return false
    if (showPlaceholder) return false
    if (isMobile) return false

    return showToolbar
  })()

  const handleRefresh = () => {
    contentRef.current?.refresh()
  }

  const handleOpenURL = () => {
    openURLInBrowser?.(url)
  }

  return (
    <Container
      onMouseEnter={() => setShowToolbar(true)}
      onMouseLeave={() => setShowToolbar(false)}
      height={height}
    >
      <ContentWrapper>
        <TransitionGroup>
          <CSSTransition
            key={showPlaceholder.toString()}
            timeout={500}
            classNames="tran"
          >
            <Content>
              {showPlaceholder ? (
                <YoukuPlaceholder
                  isMobile={isMobile}
                  onURLChange={onURLChange}
                  onGoToSite={onGoToSite}
                  openURLInBrowser={openURLInBrowser}
                  readonly={readonly}
                  locale={locale}
                />
              ) : (
                <YoukuContent url={url} ref={contentRef} />
              )}
            </Content>
          </CSSTransition>
        </TransitionGroup>
      </ContentWrapper>

      {toolbarVisible && (
        <Toolbar onRefresh={handleRefresh} onOpenURL={handleOpenURL} />
      )}
    </Container>
  )
}
