const getIframeURL = (html: string): string => {
  try {
    const div = document.createElement('div')
    div.innerHTML = html
    return (div.children[0] as HTMLIFrameElement).src
  } catch (err) {
    return ''
  }
}

// ‒ https://ditu.amap.com/place/B0FFFKOO8E 直接从地址栏复制
// ‒ https://www.amap.com/place/B00150F6D6  直接从地址栏复制
// ‒ https://www.amap.com/search?....
// ---
// https://surl.amap.com/1u3E4YQk87c 点击分享以后得到的，这个地址无效
// 因为第一跳会跳转到 http 地址，导致在 https 站点中无法加载
const gaodePCURLRegexp = /^https:\/\/(ditu\.amap\.com|www\.amap\.com)\/.+$/

// - https://m.amap.com/detail/index/poiid=B0FFGATHDX
const gaodeMobileURLRegexp = /^https:\/\/m.amap.com\/detail\/index\/poiid=(.+?)(\?.+)?$/

export const transformGaodeInput = (input: string): string => {
  if (gaodePCURLRegexp.test(input)) return input

  if (gaodeMobileURLRegexp.test(input)) {
    return `https://ditu.amap.com/place/${
      input.match(gaodeMobileURLRegexp)![1]
    }`
  }

  return ''
}

// ‒ https://www.xiami.com/song/nno403924e3
// https://m.xiami.com/song/1978898
const xiamiURLRegexp = /^https:\/\/(www\.xiami\.com|m\.xiami\.com)\/song\/(.+?)(\?.*)?$/

// - https://www.xiami.com/webapp/embed-player?autoPlay=1&id=2105272737
const xiamiPlayerURLRegexp = /^https:\/\/www\.xiami\.com\/webapp\/embed-player\?.+$/

// 将输入的内容转换为虾米 iframe 的 URL 格式，如果不能转换，返回空
// 输入格式：
// ‒ https://www.xiami.com/song/nno403924e3
// ‒ <iframe frameborder="no" border="0" marginwidth="0" marginheight="0" width="750" height="110" loading="lazy" sandbox="allow-popups allow-scripts allow-same-origin" src="https://www.xiami.com/webapp/embed-player?autoPlay=1&id=2105272737"></iframe>
// 输出格式：https://www.xiami.com/webapp/embed-player?autoPlay=1&id=2105272737
export const transformXiamiInput = (input: string): string => {
  // iframe
  if (input.startsWith('<iframe')) {
    const url = getIframeURL(input)
    return xiamiPlayerURLRegexp.test(url) ? url : ''
  }

  const match = input.match(xiamiURLRegexp)
  if (match == null) return ''

  // autoplay=0 关闭自动播放
  return `https://www.xiami.com/webapp/embed-player?autoPlay=0&id=${match[2]}`
}

const youkuURLRegexp = /^https?:\/\/v\.youku\.com\/v_show\/id_(.+?)\.html(\?.*)?$/

const youkuPlayerURLRegexp = /^https?:\/\/player\.youku\.com\/embed\/.+$/

// 将输入的内容转换为优酷 iframe 的 URL 格式，如果不能转换，返回空
// 输入格式：
// - https://player.youku.com/embed/XMTY1MjYwODYzNg==
// - https://v.youku.com/v_show/id_XNDk0MTAzNTc5Ng==.html?spm=a2hbt.13141534.1_3.d_1_2&s=cafe37eba40744ba9ca9
// - https://v.youku.com/v_show/id_XNDk0MTAzNTc5Ng==.html
// - <iframe height=498 width=510 src='https://player.youku.com/embed/XNDk0MTAzNTc5Ng==' frameborder=0 'allowfullscreen'></iframe>
// 输出格式： https://player.youku.com/embed/XNDgzNDM2NTM1Ng==
export const transformYoukuInput = (input: string): string => {
  // iframe
  if (input.startsWith('<iframe ')) {
    const url = getIframeURL(input)
    return youkuPlayerURLRegexp.test(url) ? url : ''
  }

  if (youkuPlayerURLRegexp.test(input)) {
    return input
  }

  const match = input.match(youkuURLRegexp)
  if (match == null) return ''
  return `https://player.youku.com/embed/${match[1]}`
}

const gistUrlRegexp = /^https?:\/\/gist\.github\.com\/.+$/
export const transformGistInput = (input: string) => {
  if (gistUrlRegexp.test(input)) {
    return input
  }
  return ''
}

const framerUrlRegexp = /^https?:\/\/framer\.cloud\/.+$/
export const transformFramerInput = (input: string) => {
  if (framerUrlRegexp.test(input)) {
    return input
  }
  return ''
}

const figmaUrlRegexp = /^https?:\/\/www\.figma\.com\/.+$/
export const transformFigmaInput = (input: string) => {
  if (figmaUrlRegexp.test(input)) {
    return `https://www.figma.com/embed?embed_host=oembed&url=${input}`
  }
  return ''
}

function CreateJsonpCallbackKey(val: number) {
  return 'urlcard_jsonp_callback_' + val
}

let count = 0
let jsonpCache = new Map<string, any>()
export function CreateJSONP<T>(url: string): Promise<T> {
  if(jsonpCache.get(url)){
    return new Promise((resolve) => resolve(jsonpCache.get(url)))
  }
  const script = document.createElement('script')
  const jsonpCallbackKey = CreateJsonpCallbackKey(count++)

  const destroyFn = () => {
    script.parentElement && script.parentElement.removeChild(script)
    delete (window as any)[jsonpCallbackKey]
  }

  script.src = `${url}?callback=${jsonpCallbackKey}`
  script.id = jsonpCallbackKey

  return new Promise((resolve, reject) => {
    (window as any)[jsonpCallbackKey] = (res: T) => {
      jsonpCache.set(url, res)
      resolve(res)
      destroyFn()
    }
    script.onerror = (error) => {
      reject(error)
      destroyFn()
    }
    document.getElementsByTagName('head')[0].appendChild(script)
  })
}

export function applyGistStylesheet(url: string) {
  const id = 'embed-gist-link'
  if (!document.getElementById(id)) {
    const link = document.createElement('link')

    link.href = url
    link.rel = 'stylesheet'
    link.id = id

    document.getElementsByTagName('head')[0].appendChild(link)
  }
}

// 检测style是否加载
export function afterGistStylesheet(url: string) {
  const img = document.createElement('img');
  return new Promise((resolve) => {
    img.onerror = () => {
      resolve('')
    }
    img.src = url;
  })
}
